body
{
    margin: 0px ;
}
 .header
{
height: 60px;
background-color: rgba(255, 255, 255, 0.95);
} 
.header
{
    justify-content: space-between;
} 
 .header-brand
{
    display: flex;
    background-color: rgba(255, 255, 255, 0.95);
    height: 70px;
    width:100%;
    /* position: fixed; */
    padding: 15px 120px 0px 120px;  
}
.logo
{
  height: 50px;
 width: 150px;
 font-weight: bold;
}
.nav-menu
{
   
    display: flex;
    margin-left: 150px;
     
  
}
.nav-items
{
    list-style-type:none;
    padding-right: 20px;
    position: relative;
    height:40px;
}
.nav-link
{         
    text-decoration-line: none;
    color: #333333;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.643;
    font-family: Roboto, sans-serif;
    letter-spacing: 0em;
    box-sizing: inherit;
    text-transform: uppercase;
}
.nav-link:active
{
    border-bottom: 2px solid rgb(255, 107, 106);
    
}
.departement-items
{
    position: absolute;
    display: none;
    margin-top: 0px;
    width: 180px;
    height: auto;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);  
    z-index: 1; 
    padding:10px 10px 10px 10px;  
    
}

.nav-items:hover>.departement-items,.departement-items:hover
{
    display: flex;
    flex-direction: column;
}
.nav-link:hover
{
   border-bottom: 2px solid rgb(255, 107, 106);
   color: #ababab;
}
.nav-link:visited 
{
   text-decoration-color: none;
}

.departement-item
{

    padding:10px 0px 10px 0px;    
    list-style-type:none;


}
.departement-item-link
{   
    text-decoration-line: none;
    font-family: Helvetica Neue,Helvetica,Arial,sans-serif;
    color: #333333;
    font-weight: bold; 
    font-family: 'PT Sans', sans-serif;
}
.departement-item-link:hover
{
    color: rgb(255, 107, 106);
    text-decoration-line: none;
}
.nav-compte
{
  display: flex;
  margin-top: 10px;
  margin-left: 150px;
}
.search-div
